<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="creditCardTableData" :noDataText="creditCardTableNoDataText" @selected="selectRow" search>
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadCreditCardTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="creditCardTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mr-2">{{ creditCardTablePagingInfo }}</span>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="cardNumber">
            {{ this.$t('CARD_NUMBER') }}
          </vs-th>
          <vs-th sort-key="embossedName">
            {{ this.$t('NAME_ON_CARD') }}
          </vs-th>
          <vs-th sort-key="expiryDate">
            {{ this.$t('EXPIRY_DATE') }}
          </vs-th>
          <vs-th sort-key="creditLimit">
            {{ this.$t('CREDIT_LIMIT') }}
          </vs-th>
          <vs-th sort-key="availableCreditAmount">
            {{ this.$t('AVAILABLE_CREDIT') }}
          </vs-th>
          <vs-th sort-key="statusName">
            {{ this.$t('STATUS') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].cardNumber">
              {{ data[index].cardNumber }}
            </vs-td>
            <vs-td :data="data[index].embossedName">
              {{ data[index].embossedName | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].expiryDate">
              {{ data[index].expiryDate | formatUnixDate }}
            </vs-td>
            <vs-td :data="data[index].creditLimit">
              {{ data[index].creditLimit | currency('') | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].availableCreditAmount">
              {{ data[index].availableCreditAmount | currency('') | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].statusName">
              {{ data[index].statusName | placeholder('-') }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import CreditCardTableMixin from '@/mixins/card/CreditCardTableMixin'

export default {
  name: 'credit-card-popup',
  mixins: [CreditCardTableMixin, PopupMixin],
  mounted: function () {
    this.loadCreditCardTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
