import CardService from '@/services/card/CardService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let CreditCardStatementTableMixin = {
  mixins: [ListLayoutMixin, LoadingMixin],
  data: function () {
    return {
      statementTableData: [],
      statementTableContainer: 'loading-container',
      statementTableBusy: false,
      statementTableTotalRows: 0,
      statementTableCurrentPage: 1,
      statementTablePerPage: 10
    }
  },
  computed: {
    statementTableNoDataText: function () {
      return this.statementTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    statementTableTotalPages: function () {
      return Math.ceil(this.statementTableTotalRows / this.statementTablePerPage)
    },
    statementTablePagingInfo: function () {
      return this.pagingInfo(this.statementTableCurrentPage, this.statementTablePerPage, this.statementTableTotalRows)
    }
  },
  methods: {
    loadStatementTableData (loadingContainerId) {
      let self = this
      this.statementTableBusy = true
      this.statementTableContainer = loadingContainerId || this.statementTableContainer
      this.startLoading(this.statementTableContainer)
      CardService.officialStatements({
        cardId: this.cardId
      }).then(function (response) {
        self.stopLoading(self.statementTableContainer)
        self.statementTableBusy = false
        self.statementTableTotalRows = response.data.totalRows
        self.statementTableData = response.data.statementList || []
      }).catch(function (error) {
        self.stopLoading(self.statementTableContainer)
        self.statementTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshStatementTable () {
      this.loadStatementTableData()
    }
  },
  watch: {
    statementTableCurrentPage: function () {
      this.refreshStatementTable()
    },
    statementTablePerPage: function () {
      this.refreshStatementTable()
    }
  }
}

export default CreditCardStatementTableMixin
