<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card id="loading-container" class="vs-con-loading__container mb-base">
      <div class="flex justify-between">
        <h4 class="mb-4">{{ $t('SEARCH_CRITERIA') }}</h4>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('CARD') }}</label>
          <credit-card-lookup :title="$t('CARD')" valueField="cardId" displayField="cardNumber" @select-record="selectCardRecord"/>
        </div>
      </div>

      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-end">
            <vs-button class="py-3" @click="showStatement">{{ $t('SUBMIT') }}</vs-button>
          </div>
        </div>
      </div>
    </nv-card>

    <div v-if="statementTableVisible && !statementTableBusy">
      <nv-card>
        <h4 class="mb-base">{{ $t('PAGE_TITLE') }}</h4>

        <vs-alert :active.sync="statementTableData.length === 0" color="warning">
          <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
        </vs-alert>

        <vs-table class="table-no-header" :data="statementTableData" v-if="statementTableData.length > 0">
          <template slot="thead">
            <vs-th sort-key="statementId">
              {{ this.$t('STATEMENT_ID') }}
            </vs-th>
            <vs-th sort-key="cardNumber">
              {{ this.$t('CARD_NUMBER') }}
            </vs-th>
            <vs-th sort-key="statementDate">
              {{ this.$t('STATEMENT_DATE') }}
            </vs-th>
          </template>

          <template slot-scope="{data}">
            <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
              <vs-td :data="data[index].statementId">
                <a :href="downloadStatement(data[index])" target="_blank">{{ data[index].statementId | truncate(9) }}</a>
              </vs-td>
              <vs-td :data="data[index].cardNumber">
                {{ data[index].cardNumber | placeholder('-') }}
              </vs-td>
              <vs-td :data="data[index].statementDate">
                {{ data[index].statementDate }}
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </nv-card>
    </div>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import CreditCardStatementTableMixin from '@/mixins/card/CreditCardOfficialStatementTableMixin'
import CreditCardLookup from '@/views/card/credit/credit-card-root/CreditCardLookup'

export default {
  name: 'credit-card-official-statement',
  mixins: [AlertMixin, CreditCardStatementTableMixin],
  components: {
    CreditCardLookup
  },
  data: function () {
    return {
      cardId: '',
      statementTableVisible: false
    }
  },
  methods: {
    showStatement () {
      if (this.cardId) {
        this.statementTableVisible = true
        this.loadStatementTableData()
      }
    },
    selectCardRecord (record) {
      this.cardId = record.cardId
    },
    downloadStatement: function (item) {
      let baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : ''
      let appId = this.$store.getters.appId
      let customerId = this.$store.getters.activeExternalId.externalId
      return `${baseUrl}/apps/Connect/DownloadSecure/downloadOfficialCreditCardStatement?sourceAppId=${appId}&customerId=${customerId}&cardId=${item.cardId}&statementId=${item.statementId}`
    }
  },
  watch: {
    cardId: function () {
      this.statementTableVisible = false
      this.statementTableData = []
      this.statementTableTotalRows = 0
    }
  }
}
</script>
